.cc__coverageItem{
    color:$black;

    line-height: 200%;
    display:flex;
    flex-direction: row;
    align-items: center;

    >div:first-of-type{
        flex:0 0 60px;
        margin-right: 20px;
    }
    >div:last-of-type{
        flex:1
    }
    sup{
        word-break:keep-all;
    }
    @include media-breakpoint-down(md) {
        font-size:18px;
        >div:first-of-type{
            margin-left: 20px;
        }
        +.cc__coverageItem{
            border-top: 1px dotted #999;
            margin-top: 16px;
            padding-top: 16px;
        }
    }
    @include media-breakpoint-only(md) {
        font-size:20px;

        +.cc__coverageItem{
            margin-top: 25px;
            margin-top: 16px;
            padding-top: 16px;
        }
    } 
    @include media-breakpoint-up(lg) {
        font-size:22px;

        +.cc__coverageItem{
            margin-top: 25px;
            margin-top: 16px;
            padding-top: 16px;
        }
    }
}

