.vaccine__top {
	overflow: hidden;

    @include media-breakpoint-up(lg) {
        padding-top: 52px;
    }
}
.vaccine__top-inner {
	position: relative;
	max-width: 1366px;
	// height: 500px;
	height: 520px;
	margin: 0 auto;
	/*box-shadow: inset 0 0 0 2px black;*//*debug*/
	font-family: "Noto Sans TC", "Microsoft JhengHei", "微軟正黑體", sans-serif;
}
.vaccine__top * { box-sizing: border-box; }

.vaccine__top-cont {
	position: absolute;
	z-index: 1;
	left: 82px;
	// top: 68px;
	top: 40px;
	width: 470px;
}
	.vaccine__top-maintitle {
		display: block;
		height: 118px;
		padding: 0;
		margin-top: 0;
		margin: 0 0 80px 0;

	}
	.vaccine__top-maintitle-span{
		text-indent: -99999px;
		display: block;
		height: 0;
	}
    .vaccine__top-maintitle img { display: block; height: 100%; width: auto; margin: 0 auto; }
		
	.vaccine__top-list {
		display: flex;
		justify-content: space-between;
		padding: 0; margin: 0;
	}
		a.vaccine__top-item {
			display: block;
			position: relative;
			width: 226px;
			// padding: 15px 10px 54px;
			margin: 0;
			list-style: none;
			border-radius: 20px;
			overflow: hidden;
			background: #fff;
			// box-shadow: inset 0 0 0 2px #ea596d;
			// border:2px solid #ea596d;
			background-color: #ea596d;
			padding:2px;
			text-align: center;
			color: #333;
			font-size: 22px;
			text-decoration: none!important;
			user-select: none;
			transition: .12s;
		}
			.vaccine__top-item-title {
				border-top-left-radius: 18px;
				border-top-right-radius: 18px;
				display: block;
				margin:0;
				padding: 15px 0 0;
				// margin: 0 10px 5px;
				background-color: #fff;
				font-size: 30px;
				line-height: 30px;
				font-weight: 900;
				color: #ea596d;
				-moz-osx-font-smoothing: grayscale;
				-webkit-font-smoothing: antialiased;
				font-smooth: always;
			}
			.vaccine__top-item-title span{
				border-bottom: 1px solid #ea596d;
				display: block;
				margin: 0px 20px;
				padding-bottom: 10px;
			}
				
			.vaccine__top-item-descr {
				// margin: 0 0 12px 0; 
				padding: 10px 0 60px;
				line-height: 1.4;
				background-color: #fff;
			}
				.vaccine__top-item-descr em {
					line-height: 1;
					font-weight: 900;
					font-size: 36px;
                    margin-right: 4px;
					font-style: italic;
					color: #ea596d;
					-moz-osx-font-smoothing: grayscale;
					-webkit-font-smoothing: antialiased;
					font-smooth: always;
				}
			.vaccine__top-item-btn {
				position: absolute;
				left: 0; right: 0; bottom: 0;
				display: flex;
				align-items: center;
				justify-content: center;
				height: 54px;
				padding-bottom: .2em;
				background: #ea596d;
				letter-spacing: 0.03em;
				color: #fff;
				transition: .12s;
			}
			.vaccine__top-item-btn i { /* icon */
				display: inline-block;
				margin-left: .5em;
				width: 10px;
				height: 10px;
			}
				.vaccine__top-item-btn i:after{
					content: ""; display: block;
					width: 100%; height: 100%;
					position: relative;
					border: 2px solid white;
					border-width: 2px 2px 0 0;
					transition: border-color .12s ease;
				}
				.vaccine__top-item-btn i:after {
					transform: rotate(45deg);
					margin-left: -15%;
				}
	
		a.vaccine__top-item:first-child {background-color: #35a4b5;} // box-shadow: inset 0 0 0 2px #35a4b5; }
			a.vaccine__top-item:first-child .vaccine__top-item-title { color: #35a4b5; }
			a.vaccine__top-item:first-child .vaccine__top-item-title span { border-color: #35a4b5; }
			a.vaccine__top-item:first-child .vaccine__top-item-descr em { color: #35a4b5; }
			a.vaccine__top-item:first-child .vaccine__top-item-btn { background: #35a4b5; }
			
		a.vaccine__top-item:hover,
		a.vaccine__top-item:active {
			opacity: 1;color:#333; 
			// background: #f8f8f8; 
		}
			a.vaccine__top-item:hover .vaccine__top-item-btn,
			a.vaccine__top-item:active .vaccine__top-item-btn { 
				background: #ff3b56; 
				text-indent: 6px;
			 }
		
		a.vaccine__top-item:first-child:hover,
		a.vaccine__top-item:first-child:active {  }
			.vaccine__top-item:first-child:hover .vaccine__top-item-btn,
			.vaccine__top-item:first-child:active .vaccine__top-item-btn { background: #3cb7ca; }
		
.vaccine__top-pic {
	position: absolute;
	right: 45px;
	bottom: 0;
	width: 725px; /* 725x460*/
	height: 460px;
	margin: auto;
	pointer-events: none;
	user-select: none;
}
	
	.vaccine__top-bg {
		position: absolute;
		left: 0; right: 0; top: 0; bottom: 0; margin: auto;
		width: 2px; height: 2px;
	}
		.vaccine__top-bg div {
			position: absolute;
			left: 50%; top: 50%;
			width: 900px; height: 900px;
			margin: -380px 0 0 -450px;
		}
		.vaccine__top-bg div * {
			display: block;
			position: absolute;
			left: 0; right: 0; top: 0; bottom: 0; margin: auto;
			border-radius: 100%;
		}
			.vaccine__top-bg i { width: 100%; height: 100%; background: #ddf2f2; }
			.vaccine__top-bg b { width: 88%; height: 88%; background: #eaf7f7; }
			.vaccine__top-bg span { width: 60%; height: 60%; background: #fff;  }
	
	.vaccine__top-inj {
		position: relative;
		height: 0;
		padding-top: 63.44828%;
		/*outline: 1px solid black;*//*debug*/
	}
		.vaccine__top-inj * {
			/*outline: 1px solid blue;*//*debug*/
		}
		.vaccine__top-inj img {
			display: block;
			width: 100%; height: 100%;
		}
			.vaccine__top-inj-arm {
				position: absolute;
				left: 45.3793%;
				top: 59.1304%;
				width: 33.2413%;
				height: 34.5652%;
			}
			.vaccine__top-inj-patient {
				position: absolute;
				left: 0; top: 0;
				width: 57.37931%;
				height: 100%;
			}
			.vaccine__top-inj-nurse {
				position: absolute;
				left: 57.37931%; top: 0;
				width: 42.620689%;
				height: 100%;
			}

/* ----------- rwd -----------*/
@media (max-width: 1280px) {
	.vaccine__top-cont { left: 40px; }
	.vaccine__top-pic { right: -10px; }
}
@media (max-width: 1152px) {
	.vaccine__top-pic { right: -90px; }
}
@media (max-width: 1024px) {
	.vaccine__top-item-descr {
		padding: 10px 0 50px;
	}
	// .vaccine__top-inner { height: 380px; }
	.vaccine__top-inner { height: 460px; }
	.vaccine__top-cont { top: 40px; width: 420px; }
		.vaccine__top-maintitle { height: 90px; margin-bottom: 90px; }
		a.vaccine__top-item { width: 200px; 
			// padding: 10px 10px 42px; 
			font-size: 18px; }
		.vaccine__top-item-btn { height: 42px;  }
	
	.vaccine__top-pic { right: -30px; width: 580px; height: 368px; }
		.vaccine__top-bg div { width: 700px; height: 700px; margin: -250px 0 0 -350px; }
}
@media (max-width: 960px) {
	.vaccine__top-pic { right: -100px; }
}
/* mobile */
@media (max-width: 820px) {
	.vaccine__top-inner {
		height: auto;
		padding-top: 10px;
		display: flex;
		flex-direction: column;
		align-items: center;
	}
	.vaccine__top-cont { position: relative; left: auto; top: auto; margin: 0 auto 20px;  }
	.vaccine__top-pic { position: relative; right: auto; bottom: auto; margin: 0 auto;  }
}
@media (max-width: 432px) {
	.vaccine__top-cont { width: 350px; }
		.vaccine__top-maintitle { height: 80px; margin-bottom: 80px; }
		a.vaccine__top-item { width: 170px; font-size: 16px; }
		.vaccine__top-item-title { padding: 12px 0 0; font-size: 21px; }
		.vaccine__top-item-title span{margin:0 15px;}
			.vaccine__top-item-descr em { font-size: 21px; }
	
	.vaccine__top-pic { width: 360px; height: 228px; }
		.vaccine__top-bg div { width: 500px; height: 500px; margin: -190px 0 0 -250px; }
}

/* ----------- animation  -----------*/
.vaccine__top-maintitle {
	animation:
		vacc-fade-in .4s ease-in-out 0s backwards,
		vacc-slide-bottom-in .6s ease-in-out 0s backwards;
}

.vaccine__top-sub {
	animation:
		vacc-fade-in .5s ease-in-out 0s  backwards,
		vacc-sub-slide-bottom-in .6s ease-in-out 0s backwards;
}
.vaccine__top-sub-title i{
	animation:
		vacc-arrow-in .6s ease-in .6s  backwards,
}

.vaccine__top-list {
	perspective: 900px;
	animation:
		vacc-fade-in .5s ease-out 0s backwards,
		vacc-list-in .75s ease-in-out 0s backwards;
}
.vaccine__top-item:first-child {
		transform-origin: 90% 50%;
		animation:
			vacc-item1-in .75s ease-in-out 0s backwards;
	}
	.vaccine__top-item {
		transform-origin: 10% 50%;
		animation:
			vacc-item2-in .75s ease-in-out 0s backwards;
	}

.vaccine__top-bg div * {
	animation:
		vacc-fade-in 1s ease-out 0s backwards,
		vacc-scale-in 1.2s cubic-bezier(.38,0,.45,1) 0s backwards,
		vacc-breath 1s cubic-bezier(.38,0,.45,1) 0s alternate infinite forwards;
}
	.vaccine__top-bg i { animation-delay: 	.1s, .1s, 1.8s; }
	.vaccine__top-bg b { animation-delay: 	.3s, .3s, 1.85s; }
	.vaccine__top-bg span { animation-delay: 	.5s, .5s, 1.9s; }


.vaccine__top-inj {
	animation:
		vacc-fade-in 1s ease-in-out 					.6s backwards,
		vacc-slide-bottom-in 1.5s ease-in-out 			.6s backwards;
}
	.vaccine__top-inj-nurse {
		transform-origin: 0% 90%;
		animation:
			vacc-nurse-in 1.2s ease-in-out 				.6s backwards;
	}
	.vaccine__top-inj-arm {
		transform-origin: 70% 100%;
		animation:
			vacc-arm-in 3s cubic-bezier(.38,0,.45,1) 	.6s backwards;
	}
	
@keyframes vacc-fade-in {
	0% { opacity: 0; }
	100% { opacity: 1; }
}	
@keyframes vacc-scale-in {
	0% {transform: scale(0); }
	100% { transform: scale(1); }
}
@keyframes vacc-list-in {
	0% {transform: scale(.6); }
	100% { transform: scale(1); }
}
@keyframes vacc-item1-in {
	0% { transform: scale(.7) rotateY(90deg); }
	100% { transform: scale(1) rotateY(0deg); }
}
@keyframes vacc-item2-in {
	0% { transform: scale(.7) rotateY(-90deg); }
	100% { transform: scale(1) rotateY(0deg); }
}
@keyframes vacc-slide-bottom-in {
	0% {transform: translateY(50%); }
	100% { transform: translateY(0px); }
}	
	
@keyframes vacc-sub-slide-bottom-in {
	0% {transform: translateY(100%); }
	100% { transform: translateY(0px); }
}	

@keyframes vacc-nurse-in {
	0% { transform: translateX(-5px) skew(7deg, -7deg); }
	100% { transform: translateX(0px) skew(0deg, 0deg); }
}
@keyframes vacc-arm-in {
	0% { transform: rotate(8deg); }
	40% { transform: rotate(13deg); }
	100% { transform: rotate(0deg); }
}
@keyframes vacc-breath {
	0% { transform: scale(1); opacity: 1; }
	100% { transform: scale(1.08); opacity: .8; }
}

@keyframes vacc-arrow-in {
	0% { transform: scale(0,0) rotate(45deg); opacity: 1; }
	100% { transform: scale(1,1)  rotate(-15deg); opacity: .8; }
}
	
	
	
	

.vaccine__top-sub {
	letter-spacing:0;
	margin:24px 0;
	display:flex;
	align-items: center;
	justify-content: center;
	.cc__btn{
		padding:10px;
		height: 40px;
		&.cc--arrow span+i{
			margin-left: 10px;
			padding-right: 10px;
		}
	}
}
.vaccine__top-sub-title{
	line-height: 1.3;
	b{
		font-weight: 700;
		color:#35a4b5;
	}
	position: relative;


	i{
		// transform: scale(1,1);
		transition: transform .5s ease-out;
		transform-origin: 100% 0%;
		transform: scale(1,1)  rotate(-15deg);
		position: absolute;
		display: block;
		top:10px;
		left:-40px;
		width: 30px;
		height: 33px;
		background-size:cover;
		background-image: url(data:image/svg+xml;base64,);
	}
	@include media-breakpoint-down(lg) {
		font-size: 18px;
		flex-basis: 50%;
		max-width: 50%;
		i{
			top:10px;
			left:-35px;
		}
    }
    @include media-breakpoint-up(lg) {
		font-size: 20px;
		flex-basis:55%;

		i{
			top:10px;
			left:-50px;
			width: 40px;
			height: 44px;
		}
    }
    @include media-breakpoint-up(xl) {
		flex-basis:auto;
    }
}