@import "../../bootstrap5/functions";
@import "../../bootstrap5/variables"; 
@import "../../bootstrap5/mixins";
@import "../../variables";
@import "../../mixins";

@import "../covidvaccine/variables";

.cc__section--info{
    background: linear-gradient(180deg, rgba(255,255,255,1) 0%, rgba(248,248,248,1) 100%);
    @include media-breakpoint-down(md) {

        // padding-bottom: 200px;
        padding-bottom: 55%;
    }

    // @include media-breakpoint-up(xl) {

    //     padding-bottom: 200px;
    // }
    overflow:hidden;
}

.cc__info{
    
    position: relative;
    z-index: 2;
    @include media-breakpoint-up(md) {
        display:flex;
        flex-direction: row;
    }
    
    @include media-breakpoint-only(md) {
        margin:0 -10px;
    }
    
    @include media-breakpoint-up(lg) {
        margin:0 -20px;
    }
}
.cc__infoItem{

    position: relative;
    margin-bottom: 40px;
    @include media-breakpoint-only(md) {
        padding:0 10px;
    }
    
    @include media-breakpoint-up(md) {
        flex:1;
    }
    @include media-breakpoint-up(lg) {
        padding:0 20px;
    }
}

.cc__infoItemWrap{
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    padding:2px;
    overflow:hidden;
}
.cc__infoRow{
    display:flex;
    flex-direction:row;
    flex-wrap: wrap;
    margin-top: 2px;
    &:first-of-type{
        margin-top: 0;
    }
    &:last-of-type{
        margin-top: 0;
    }
    &.cc__infoRow--mt{
        margin-top: 24px;
    }

    &.cc__infoRow--blue{
        
        @include media-breakpoint-down(lg) {
            justify-content: center;
            .cc__btn{
                margin:20px 0;
            }
        }
        @include media-breakpoint-up(md) {
            // .cc__btn{
            //     margin:0 0 0 24px;
            // }
        }
    }
    @include media-breakpoint-down(sm) {
    }
    @include media-breakpoint-between(sm,lg) {
    }
    @include media-breakpoint-up(lg) {
        flex-wrap: nowrap;
    }

    @include media-breakpoint-only(xl) {
    }
}
.cc__infoCol{
    position: relative;
    // border-radius: 10px;
    
    .cc__infoItem--red &{
        +.cc__infoCol{
            box-shadow: inset 2px 0 0 #ffdde2;
        }
        +.cc__infoCol--head{
            box-shadow: inset 2px 0 0 #fff;
        }
    }
    .cc__infoItem--blue &{
        +.cc__infoCol{
            box-shadow: inset 2px 0 0 #e2f8ff;
        }
        +.cc__infoCol--head{
            box-shadow: inset 2px 0 0 #fff;
        }
    }
    width: 100%;
    display:flex;
    flex-direction: column;
    @include media-breakpoint-down(md) {
        padding:20px 12px;
        &:first-of-type{
            flex:1 0 60%;
        }
        &:last-of-type{
            flex:1 1 40%;
        }
    }
    @include media-breakpoint-only(md) {
        padding:20px 12px;
        &:first-of-type{
            flex:1 0 60%;
        }
        &:last-of-type{
            flex:1 1 30%;
        }
    }
    @include media-breakpoint-up(lg) {
        padding:20px 18px;
        &:first-of-type{
            flex:1 0 60%;
        }
        &:last-of-type{
            flex:1 1 40%;
        }
    } 
}
.cc__infoTitle{
    margin:0;
    font-weight: 400;
    line-height: 1;
    color:#fff;
    text-align:center;

    @include media-breakpoint-down(lg) {
        font-size:20px;
    }
    @include media-breakpoint-up(lg) {
        font-size:24px;
    }
}
.cc__infoAction{
    display:flex;
    align-items: center;

    .cc__btn{
        margin-left:12px;
    }
}
.cc__infoField{
    font-weight: 500;;
    @include media-breakpoint-down(lg) {
        font-size:16px;
    }
    @include media-breakpoint-up(lg) {
        font-size:20px;
    }
}

.cc__infoEvent{
    margin:4px 0;
    @include media-breakpoint-down(lg) {
        font-size:16px;
    }
    @include media-breakpoint-up(lg) {
        font-size:20px;
    }
}
.cc__infoSubtitle{
    margin:4px 0;
    @include media-breakpoint-down(lg) {
        font-size:16px;
    }
    @include media-breakpoint-up(lg) {
        font-size:20px;
    }
}
.cc__infoLimit{
    @include media-breakpoint-down(lg) {
        font-size:22px;
    }
    @include media-breakpoint-up(lg) {
        font-size:26px;
    }
}
.cc__infoNote{
    margin:12px 0 4px;
    color:#666666;
    @include media-breakpoint-down(lg) {
        font-size:14px;
    }
    @include media-breakpoint-up(lg) {
        font-size:16px;
    }
    &.cc__infoNote--important{
        color:#ea596d;
        text-align: left;
        display:flex;
        line-height: 1.4;
        >span{
            margin-right: 4px;
        }
    }  
}


.cc__infoQuote{
    line-height:1;
    @include media-breakpoint-down(lg) {
        font-size:20px;
    }
    @include media-breakpoint-up(lg) {
        // font-size:24px;
        font-size:20px;
    }   
}

.cc__infoPrice{
    line-height:1;
    font-style: italic;
    font-weight: 900;
    margin:0 4px;
    @include media-breakpoint-down(lg) {
        font-size:32px;
    }
    @include media-breakpoint-up(lg) {
        font-size:40px;
    }   
}
.cc__infoBr{
    @include media-breakpoint-down(xl) {
        display: block;
    }

    
}

.cc_remark--info{
    
    @include media-breakpoint-down(lg) {
        max-width: 400px;
    }
    @include media-breakpoint-up(lg) {

    }
}

.cc__infoBg{
    z-index: 1;
    pointer-events: none;
    img{
        width: 100%;
    }
    position: absolute;
    @include media-breakpoint-down(sm) {
        width: 100%;
        left:0;
        right: 0;
        margin: 0 auto;
    }
    @include media-breakpoint-between(sm,md) {
        width: 90%;
        left:0;
        right: 0;
        margin: 0 auto;
        bottom:-9%;
        // max-width: 480px;
    }
    
    @include media-breakpoint-between(md,lg) {
        width: 450px;
        right: 0;
        bottom: -20px;
        left: 345px;
        margin: 0 auto;
    }
    @include media-breakpoint-between(lg,xxl) {
        width: 565px;
        right: 0;
        bottom: -200px;
        left: 480px;
        margin: 0 auto;
    }  
    @include media-breakpoint-up(xxl) {
        width: 630px;
        right: 0;
        bottom: -220px;
        left: 860px;
        margin: 0 auto;
    }   
}

.cc__infoBg--without-besafe{

    
    @include media-breakpoint-between(md,lg) {
        bottom: -70px;
        left: 365px;
    }
    @include media-breakpoint-between(lg,xxl) {
        bottom: -190px;
        left: 480px;
    }  
    @include media-breakpoint-up(xxl) {
        width: 565px;
        bottom: -190px;
        left: 480px;
    }  

}
.cc__infoItem--purple{
    border-radius:10px;
    // overflow:hidden;
    border:2px solid $purple;
    margin-bottom: 0;
    position: relative;
    z-index: 2;
    background-color: #fff;
    
    @include media-breakpoint-down(md) {
        // 
        width: 100%;
    }
    
    @include media-breakpoint-between(md,lg) {
        max-width: 370px;
    }

    @include media-breakpoint-between(lg,xxl) {
        max-width: 550px;
    }
    @include media-breakpoint-up(xxl) {
        max-width: 760px;
        padding:0;
    }
}

a.cc__infoLink{
    font-size:16px;
    line-height: 1;
    text-decoration: underline;
    text-align: center;
    width: 100%;
    display: block;
    margin:12px 0;
    @include media-breakpoint-down(lg) {
    }
    @include media-breakpoint-up(lg) {
        font-size:16px;
    }  
} 
a.cc__infoLink--blue{
    color:$blue;

    &:hover{
        color:$blue_hover;
    }

}
a.cc__infoLink--red{
    color:$red;

    &:hover{
        color:$red_hover;
    }

}
.cc_infoBesafeComma{
    @include media-breakpoint-down(xxl) {
        display: none;

    }
    @include media-breakpoint-up(xxl) {
        display: inline-block;
    }
}
.cc__infoIcon{
    position: absolute;
    width: 55px;
    transform:rotate(-30deg);
    @include media-breakpoint-down(md) {
        left:-5px;
        top:-25px;
    }
    @include media-breakpoint-up(md) {
        left:-25px;
        top:-15px;
    }
}
.cc__infoBesafeLabel{
    align-items: baseline;
    display:flex;

    @include media-breakpoint-down(lg) {
        margin-bottom: 12px;
    }
   
    @include media-breakpoint-down(xxl) {
        // 
        flex-direction: column;
        align-items: center;

        >div:first-of-type{
            margin:4px 0;
            font-weight: 500;
        }
        >div:last-of-type{
            margin:4px 0;
            font-weight: 500;
        }
    }
    @include media-breakpoint-down(xxl) {
        font-size:22px;
    }
    @include media-breakpoint-up(xl) {
    }
    @include media-breakpoint-up(xxl) {
        font-size:28px;
        margin-right: 12px;
        flex-direction: row;
    }
}
.cc__infoBesafe{
    display:flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    padding:16px 20px;
    
    @include media-breakpoint-down(lg) {
        flex-direction: column;
        .cc__infoAction .cc__btn{
            margin-left:0;
        }
    }
    @include media-breakpoint-up(lg) {
        flex-direction: row;
    }
    @include media-breakpoint-up(lg) {
    }  

}