@import "../../bootstrap5/functions";
@import "../../bootstrap5/variables"; 
@import "../../bootstrap5/mixins";
@import "../../variables";
@import "../../mixins";


// @import "base";
//container

.cc__notificationItem{
    font-weight: 400;
    line-height: 150%;
    color:#666666;
    +.cc__notificationItem{
        margin-top:8px;
    }
    
    @include media-breakpoint-down(md) {
        font-size:16px;
    }
    @include media-breakpoint-up(md) {
        font-size:18px;
    }
}
.cc__notificationContent{
    // height: 150px;
    overflow:hidden;
    transition:max-height .3s ease-in;
    width: 95%;
}
.cc__notificationOl{
    list-style:decimal;
    padding-left: 1.5rem;
}

.cc__notificationAction{
    text-align: center;
    position: relative;
    

    &:before{
        pointer-events: none;
        content:"";
        position: absolute;
        top:0;
        left:0;
        right:0;
        width:100%;
        height:100px;
        background: linear-gradient(180deg, rgba(255,255,255,0) 0%, rgba(255,255,255,0) 50%, rgba(255,255,255,1) 100%);
        transform: translateY(-100%);
        transition:opacity .5s ease-in;
    }

    &.cc__notificationAction--active{
        &:before{
            opacity:0;
        }
    }
}

a.cc__notificationMore{
    display: inline-flex;
    align-items: center;
    font-weight: 400;
    color:#35a4b5;
    text-align: center;
    text-decoration: underline;
    vertical-align: middle;
    cursor: pointer;
    user-select: none;
    border:0;
    // border-bottom:1px solid #237e8c;
    padding:0;
    font-size:18px;
    line-height: 1;

  
    &:hover {
        color:#237e8c;
      text-decoration: none;
    }
  
    .btn-check:focus + &,
    &:focus {
      outline: 0;
    }
  
    .btn-check:checked + &,
    .btn-check:active + &,
    &:active,
    &.active {
  
      &:focus {
      }
    }
  
    &:disabled,
    &.disabled,
    fieldset:disabled & {
      pointer-events: none;
      opacity: $btn-disabled-opacity;
    }


    i{
        display: inline-block;
        width: 15px;
        height: 15px;
        position: relative;
        &:before, &:after {
            position: absolute;
            top:1px;
            right:0;
            content: ' ';
            height: 15px;
            width: 2px;
            background-color: #35a4b5;
            transition: opacity  .5s ease-in-out, transform .5s ease-in-out;
        }
        &:before {
            transform: rotate(90deg);
        }
        &:after {
            transform: rotate(0deg);
        }
    }
    &.cc__notificationMore--active{
        i{
            &:before {
                opacity:0;
                transform: rotate(270deg);
            }
            &:after {
                transform: rotate(270deg);
            }
        }
    }
}