@import "../../bootstrap5/functions";
@import "../../bootstrap5/variables"; 
@import "../../bootstrap5/mixins";
@import "../../variables";
@import "../../mixins";

@import "../covidvaccine/variables";

.cc__cathay{
    padding:60px 0 80px;
}
.cc__cathayRow{
    display:flex;
    flex-direction:row;
    flex-wrap: wrap;

    @include media-breakpoint-down(sm) {
    }
    @include media-breakpoint-between(sm,lg) {
        margin:-2%;
    }
    @include media-breakpoint-up(lg) {
        flex-wrap: nowrap;
    } 

    @include media-breakpoint-only(xl) {
    }
}
.cc__cathayBr{
    
    @include media-breakpoint-down(sm) {
        display: block;
    }
    @include media-breakpoint-between(sm,lg) {
    }
    @include media-breakpoint-up(lg) {
    } 

    @include media-breakpoint-only(xl) {
    }
}
.cc__cathayCol{
    position: relative;
    border:2px solid #d5f1f1;
    border-radius: 10px;
    

    @include media-breakpoint-down(sm) {
        width: 100%;
        padding:20px 30px 20px 50px;
        display:flex;
        flex-direction: row;
        align-items: center;
        +.cc__cathayCol{
            margin-top: 24px;
        }
    }
    @include media-breakpoint-between(sm,md) {
        display:flex;
        align-items: center;
        flex:1 1 46%;
        margin:2%;
        padding:20px 10px 20px 20px;
    }
    @include media-breakpoint-only(md) {
        display:flex;
        align-items: center;
        flex:1 1 46%;
        margin:2%;
        padding:20px 20px 20px 50px;
    }
    @include media-breakpoint-up(lg) {
        flex:1 ;
        margin:0 1%;
        padding:20px;
        text-align: center;
    } 
}
