.cc__cathayImg{
    @include media-breakpoint-down(sm) {
        width: 65px;
        margin-right: 24px;
    }
    @include media-breakpoint-between(sm,lg) {
        width: 65px;
        margin-right: 24px;
    }
    @include media-breakpoint-up(lg) {
        width: 80px;
        margin:31px 0 20px;
    }
}

.cc__cathayTitle{
    margin:0;
    line-height: 130%;
    font-weight: 500;
    color:$blue;

    +.cc__cathayDescription{
        margin-top: 8px;
    }

    @include media-breakpoint-between(sm,xl) {
        font-size:20px;
    }
    @include media-breakpoint-up(lg) {
        text-align: center;
    } 
    @include media-breakpoint-only(lg) {
        // font-size:26px;
    }
    @include media-breakpoint-only(xl) {
        font-size:26px;
    } 
}
.cc__cathayDescription{
    line-height: 150%;
    font-weight: 500;
    color:#666;

    
    @include media-breakpoint-down(sm) {
        font-size:16px;
    }
    @include media-breakpoint-between(sm,lg) {
        br{
            display: none;
        }
    }
    @include media-breakpoint-up(lg) {
        font-size:18px;
        text-align: center;
    } 
}
