@import "../../bootstrap5/functions";
@import "../../bootstrap5/variables"; 
@import "../../bootstrap5/mixins";
@import "../../variables";
@import "../../mixins";

.c-tab-inner{
    &::-webkit-scrollbar{
        display:none;
    }
    
    @include media-breakpoint-down(md) {
            margin:0 -15px;
            overflow-x:scroll;
    }
}


.c-tab-list{
    margin:0;
    padding:0;
    list-style:none;
    display:flex;
    flex-direction: row;
    flex-wrap: nowrap;
    // justify-content: space-evenly;
    width: 100%;
    position: relative;
    z-index: 190;
    margin-bottom: 20px;
    
    @include media-breakpoint-down(md) {
        width: 540px;
        padding-left: 15px;
    }
}
.c-tab-item-qty{
    margin-left: 4px;;
}
.c-tab-item{
    border-top-left-radius: 25px;
    border-top-right-radius: 25px;
    border-bottom-left-radius: 25px;
    border-bottom-right-radius: 25px;
    background-color: transparent;
    // flex:1;
    overflow: hidden;
    cursor: pointer;
    color:#35a4b5;
    display: flex;
    align-items: center;
    justify-content: center;
    border:1px solid #35a4b5;
    text-align: center;
    &:last-of-type{
        margin-right: 0;
    }
    &.c-tab-item--active{
        color: #fff;
        background-color: #35a4b5;
    }
  
    padding:10px 12px;
    margin-right: 12px;
    @media (min-width:map-get($grid-breakpoints, 'md')){
        // padding:20px 16px; 
        margin-right: 20px;
        padding:8px 24px;
    }
  
    @media (min-width:map-get($grid-breakpoints, 'lg')){
        // padding:30px 20px; 
        &:hover,&.active{
            color: #fff;
            background-color:rgba(53,164,181,.5);
        }
    }
}
.c-tab-item-qty{
    font-size:14px;
    font-weight: 400;
    line-height: 1;
    @media (min-width:map-get($grid-breakpoints, 'md')){
    }
    @media (min-width:map-get($grid-breakpoints, 'lg')){
        font-size:16px;
    }
}
.c-tab-item-title{
    font-size:14px;
    font-weight: 400;
    line-height: 1;
    @media (min-width:map-get($grid-breakpoints, 'md')){
    }
    @media (min-width:map-get($grid-breakpoints, 'lg')){
        font-size:18px;
    }
}

.c-tab-content{
    // padding:24px;
    // background-color: #fff;
    // border-bottom-left-radius: 20px;
    // border-bottom-right-radius: 20px;
    // box-shadow: 0 15px 15px rgba(0,0,0,0.07), 0 15px 15px rgba(0,0,0,0.07);
    position: relative;
    z-index: 200;
    @media (min-width:map-get($grid-breakpoints, 'md')){
        // padding:40px 28px;
    }
    @media (min-width:map-get($grid-breakpoints, 'lg')){
        // padding:80px 60px;
    }
}

.cc__faq-wrap{
    background-color: #fff;
    padding:24px;
    box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.1);
    border-radius: 20px;
    @media (min-width:map-get($grid-breakpoints, 'lg')){
        padding:56px;
    }
}


////////
.cc__faq-subtitle{
    font-weight: bold;
    font-size: 20px;
    line-height: 1.3;
    color: #00BFC1;
    text-align: center;
    margin-bottom: 20px;
    @media (min-width:map-get($grid-breakpoints, 'md')){
        font-size: 24px;
    }
    @media (min-width:map-get($grid-breakpoints, 'lg')){
        font-size: 30px;
        margin-bottom: 30px;
    }
}
.cc__faq-group{
    margin-bottom: 50px;
    &:last-of-type{
        margin-bottom: 0;
    }
}
.cc__faq-list{
    margin-bottom: 50px;
    padding:0;
    list-style: none;
    display:flex;
    flex-direction: row;
    justify-content:space-between;
    flex-wrap: wrap;
}

.cc__faq-item{
    flex-basis:100%;
    max-width: 100%;
    display: block;

    +.cc__faq-item{
        margin-top: 20px;
    }
}

.cc__faq-item-link{
    text-decoration: none;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    position: relative;
    align-items: flex-start;
    padding:20px;
    background-color:$white;
    border-radius: 10px;
    &:hover{
        cursor: pointer;
        text-decoration: none;
        &:before{
            background-color: #35a4b5;
        }
        .cc__faq-question{
            color:#35a4b5;
        }
    }
    @media (min-width:map-get($grid-breakpoints, 'lg')){
        
        // align-items: center;
        padding:32px 36px;
    }
}
.cc__faq-no{
    font-weight: 400;
    font-size: 20px;
    line-height: 1;
    color: #35a4b5;
    margin-right: 12px;
    flex-shrink: 1;
    position: relative;
    top:2px;
}
.cc__faq-question{
    margin:0;
    font-weight: 500;
    flex:1;
    color: #333;
    line-height:1.3;
    position: relative;
    display: block;
    width: 100%;
    padding-right: 28px;
    &:before, &:after {
        position: absolute;
        top:5px;
        right:0;
        content: ' ';
        height: 15px;
        width: 2px;
        background-color: #35a4b5;
        transition: opacity  .5s ease-in-out, transform .5s ease-in-out;
    }
    &:before {
        transform: rotate(90deg);
    }
    &:after {
        transform: rotate(0deg);
    }


    &.cc__faq-question--active{
        color:#35a4b5;
        &:before {
            opacity:0;
            transform: rotate(270deg);
        }
        &:after {
            transform: rotate(270deg);
        }
    }
    
    font-size: 18px;

    @media (min-width:map-get($grid-breakpoints, 'md')){
    }
    @media (min-width:map-get($grid-breakpoints, 'lg')){
        font-size: 20px;
    }
}
.cc__faq-answer{
    width: 100%;
    line-height:150%;
    position: relative;
    color:#666666;
    display: none;
    word-break: break-word;
    
    font-size:16px;
    font-weight: 400;
    padding-right: 28px;
    // margin-top: 16px;
    padding-top: 16px;
    
    flex-basis:100%;
    max-width: 100%;
    @media (min-width:map-get($grid-breakpoints, 'md')){
    }
    @media (min-width:map-get($grid-breakpoints, 'lg')){
        font-size:20px;
        // padding-left: 48px;
    }
    a{
        color:#35a4b5;
        margin:.25em;
        text-decoration: underline;
        &:hover{
            color:#246974;
        }
    }
    b{
        color:#333;
    }
    p{
        margin-bottom: 1em;

    }
    ul{
        padding-left: 1.5rem;
        >li{
            position: relative;
            &:before{
                content:'';
                display: block;
                position: absolute;
                top:11px;
                left:-1em;
                width: 5px;
                height:5px;
                border-radius: 50%;
                background-color: #35a4b5;
            }
        }
    }
    ol{
        list-style:decimal;
        padding-left: 1.5rem;
        margin-bottom: 1em;
    }
    li{
        margin-bottom: .5rem;
    }

}


.cc__faqBtn,a.cc__faqBtn{
    display: inline-flex;
    align-items: center;
    font-weight: 400;
    color: #fff;
    text-align: center;
    text-decoration: none;
    vertical-align: middle;
    cursor: pointer;
    user-select: none;
    background-color: #f6b600;
    border: 0;
    height: 60px;
    padding:10px 50px;
    border-radius: 6px;
    font-size:20px;
    line-height: 1;

  
    &:hover {
      color: #fff;
      background-color: #b9740d;
      text-decoration: none;
    }
  
    .btn-check:focus + &,
    &:focus {
      outline: 0;
      box-shadow: 0 0 0 0.25rem rgba(253,193,13,.25);
    }
  
    .btn-check:checked + &,
    .btn-check:active + &,
    &:active,
    &.active {
  
      &:focus {
      }
    }
  
    &:disabled,
    &.disabled,
    fieldset:disabled & {
      pointer-events: none;
      opacity: $btn-disabled-opacity;
    }

}

.cc__faqAction{
    text-align: center;

    +.cc__faqRemark{
        margin-top: 20px;
    }
}
.cc__faqRemark{
    text-align: center;
    color:#999999;
    font-size:14px;
    font-weight:400;
    line-height: 150%;
}