/* ----------- animation  -----------*/
.vaccine__top-maintitle {
  animation: vacc-fade-in .4s ease-in-out 0s backwards, vacc-slide-bottom-in .6s ease-in-out 0s backwards; }

.vaccine__top-sub {
  animation: vacc-fade-in .5s ease-in-out 0s  backwards, vacc-sub-slide-bottom-in .6s ease-in-out 0s backwards; }

.vaccine__top-sub-title i {
  animation: vacc-arrow-in 0.6s ease-in 0.6s backwards; }

.vaccine__top-list {
  perspective: 900px;
  animation: vacc-fade-in .5s ease-out 0s backwards, vacc-list-in .75s ease-in-out 0s backwards; }

.vaccine__top-item:first-child {
  transform-origin: 90% 50%;
  animation: vacc-item1-in .75s ease-in-out 0s backwards; }

.vaccine__top-item {
  transform-origin: 10% 50%;
  animation: vacc-item2-in .75s ease-in-out 0s backwards; }

.vaccine__top-bg div * {
  animation: vacc-fade-in 1s ease-out 0s backwards, vacc-scale-in 1.2s cubic-bezier(0.38, 0, 0.45, 1) 0s backwards, vacc-breath 1s cubic-bezier(0.38, 0, 0.45, 1) 0s alternate infinite forwards; }

.vaccine__top-bg i {
  animation-delay: .1s, .1s, 1.8s; }

.vaccine__top-bg b {
  animation-delay: .3s, .3s, 1.85s; }

.vaccine__top-bg span {
  animation-delay: .5s, .5s, 1.9s; }

.vaccine__top-inj {
  animation: vacc-fade-in 1s ease-in-out 					.6s backwards, vacc-slide-bottom-in 1.5s ease-in-out 			.6s backwards; }

.vaccine__top-inj-nurse {
  transform-origin: 0% 90%;
  animation: vacc-nurse-in 1.2s ease-in-out 				.6s backwards; }

.vaccine__top-inj-arm {
  transform-origin: 70% 100%;
  animation: vacc-arm-in 3s cubic-bezier(0.38, 0, 0.45, 1) 0.6s backwards; }

@keyframes vacc-fade-in {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes vacc-scale-in {
  0% {
    transform: scale(0); }
  100% {
    transform: scale(1); } }

@keyframes vacc-list-in {
  0% {
    transform: scale(0.6); }
  100% {
    transform: scale(1); } }

@keyframes vacc-item1-in {
  0% {
    transform: scale(0.7) rotateY(90deg); }
  100% {
    transform: scale(1) rotateY(0deg); } }

@keyframes vacc-item2-in {
  0% {
    transform: scale(0.7) rotateY(-90deg); }
  100% {
    transform: scale(1) rotateY(0deg); } }

@keyframes vacc-slide-bottom-in {
  0% {
    transform: translateY(50%); }
  100% {
    transform: translateY(0px); } }

@keyframes vacc-sub-slide-bottom-in {
  0% {
    transform: translateY(100%); }
  100% {
    transform: translateY(0px); } }

@keyframes vacc-nurse-in {
  0% {
    transform: translateX(-5px) skew(7deg, -7deg); }
  100% {
    transform: translateX(0px) skew(0deg, 0deg); } }

@keyframes vacc-arm-in {
  0% {
    transform: rotate(8deg); }
  40% {
    transform: rotate(13deg); }
  100% {
    transform: rotate(0deg); } }

@keyframes vacc-breath {
  0% {
    transform: scale(1);
    opacity: 1; }
  100% {
    transform: scale(1.08);
    opacity: .8; } }

@keyframes vacc-arrow-in {
  0% {
    transform: scale(0, 0) rotate(45deg);
    opacity: 1; }
  100% {
    transform: scale(1, 1) rotate(-15deg);
    opacity: .8; } }

