.#{$section}--cross{

    i{
        height: 15px;
        width: 15px;
        &:before, &:after {
            position: absolute;
            top:5px;
            right:0;
            content: ' ';
            height: 15px;
            width: 2px;
            background-color: #35a4b5;
            transition: opacity  .5s ease-in-out, transform .5s ease-in-out;
        }
        &:before {
            transform: rotate(90deg);
        }
        &:after {
            transform: rotate(0deg);
        }
    }
    // &.cc__faq-question--active{
    //     color:#35a4b5;
    //     &:before {
    //         opacity:0;
    //         transform: rotate(270deg);
    //     }
    //     &:after {
    //         transform: rotate(270deg);
    //     }
    // }
}
.#{$section}--arrow{
    // @include type("h6");
    i{
        $color:#fff;
        $chevronRight:url("data:image/svg+xml,<svg width='9' height='16' fill='none' xmlns='http://www.w3.org/2000/svg'><path d='M.363.727L7.636 8 .363 15.273' stroke='"+$color+"'/></svg>");
        $arrow:url("data:image/svg+xml,<svg width='32' height='10' fill='none' xmlns='http://www.w3.org/2000/svg'><path d='M26 9.5L31 5m0 0L26 .5M31 5H0' stroke='"+$color+"'/></svg>");
        display: inline-block;
        background-size: cover;
        background-repeat: no-repeat;
        width: 9px;
        height:16px; 
        background-image:escape-svg($chevronRight);
        // @include icon("arrow","#04327A");
    }

    &:hover{
        i{
            // @include icon("arrow","#fff");
        }
    }

    span+i{
        margin-left: 20px;
    }
    
    i+span{
        margin-left: 20px;
    }
}

.#{$prefix}btn,a.#{$prefix}btn{
    display: inline-flex;
    align-items: center;
    font-weight: 400;
    color: #fff;
    text-align: center;
    text-decoration: none;
    vertical-align: middle;
    cursor: pointer;
    user-select: none;
    background-color: #f6b600;
    border: 0;
    height: 60px;
    padding:10px 50px;
    border-radius: 10px;
    font-size:20px;
    line-height: 1;

    span{
        position: relative;
        top:-1px;
        transition: transform .15s;
    }
    i{
        transition: transform .15s;
    }
    &:hover {
        opacity:1;
      color: #fff;
      background-color: #b9740d;
      text-decoration: none;
      span{
        transform: translateX(5px);
      }
      i{
        transform: translateX(5px);
      }
    }
  
    .btn-check:focus + &,
    &:focus {
      outline: 0;
    //   box-shadow: 0 0 0 0.25rem rgba(253,193,13,.25);
    }
  
    .btn-check:checked + &,
    .btn-check:active + &,
    &:active,
    &.active {
  
      &:focus {
      }
    }
  
    &:disabled,
    &.disabled,
    fieldset:disabled & {
      pointer-events: none;
      opacity: $btn-disabled-opacity;
    }
}

a.#{$prefix}btn--small{
    padding:10px 14px;
}
a.#{$prefix}btn--lg-small{
    @include media-breakpoint-up(lg) {
    padding:10px 20px;
    }
}
a.#{$prefix}btn--tiny{
    @include media-breakpoint-down(md) {
        padding:10px 10px;
        height:40px;
        height: auto;
        font-size: 16px;
    }
}

a.#{$prefix}btn--blue{
    background-color:$blue;
    &:hover{
        background-color:$blue_hover;
    }
}

a.#{$prefix}btn--red{
    background-color:$red;
    &:hover{
        background-color:$red_hover;
    }
}

a.#{$prefix}btn--purple{
    background-color:$purple;
    &:hover{
        background-color: $purple_hover;
    }
}
